import { computePosition, flip, shift } from "@floating-ui/dom";
import Bowser from "bowser";

//import widget
import { formHTML } from "./form-html";
import { formCSS } from "./form-css";
//import languages
import languages from "./lang";

const { en, kr, hu, ru, hi, cz, bg, ja, gr, af, es, de, fr, it } = languages;

// Create a language map object
const languageMap = { en, kr, hu, ru, hi, cz, bg, ja, gr, af, es, de, fr, it };

// Function to retrieve the language object based on the user's language
function getLanguageObject(language) {
  return languageMap[language] || en; // Return the language object if found, otherwise fallback to English (en)
}

let pageLoadTime = 0.00;
let projectID = null
//get users preferred language
const userLanguage = navigator.language || navigator.userLanguage;
const languageCode = userLanguage.split("-")[0] || userLanguage;
const lang = getLanguageObject(languageCode);

//Array to store all front end errors
const frontErrors = [];
window.onerror = (message, source, lineno, colno, error)=> {frontErrors.push({message:message,source:source,lineno:lineno,colno:colno,error:error})};

//inject the CSS and setup project ID 
function init() {
  const styleElement = document.createElement("style");
  styleElement.id = "widget__css";
  styleElement.innerHTML = formCSS;

  document.head.insertBefore(styleElement, document.head.firstChild);

  //add event listener and grab the devId
  document.querySelectorAll("[data-widget-button]").forEach((el) => {
    el.addEventListener("click", open);
    const elemDevId = el.getAttribute('data-widget-id')
    if(elemDevId !== null && projectID === null ) projectID = elemDevId
  });

  document.querySelectorAll("[data-widget-button]").forEach((el) => {
     const elemDevId = el.getAttribute('data-widget-id');
     if(elemDevId === null && projectID!== null ) el.setAttribute('data-widget-id',projectID)
  });


}
// css injection event listener
window.addEventListener("load", ()=>{
  init();
  //calculate page load time
  pageLoadTime = (window.performance.now());
});
const containerElement = document.createElement("div");
containerElement.id = "widget__container";

//open widget function

function open(e) {
  //insert the widget and make it visible
  document.body.appendChild(containerElement);
  containerElement.innerHTML = formHTML;
  containerElement.style.display = "block";
  
  const target = (e && e.target) || document.body;
  //Calculates where to render the BOX

  computePosition(target, containerElement, {
    placement: "bottom",
    middleware: [flip(), shift({ crossAxis: true, padding: 8 })],
    strategy: "fixed",
  }).then(({ x, y }) => {
    Object.assign(containerElement.style, {
      left: `${x}px`,
      top: `${y}px`,
    });
  });

  document.getElementById("widget__close").addEventListener("click", close);

  Array.from(containerElement.getElementsByClassName("widget__radio")).forEach(
    (el) => {
      el.addEventListener("change", changeType);
    }
  );

  document.getElementById("widget__form").addEventListener("submit", submit);
  document.getElementById("widget__title").innerHTML = lang.titles.default;
  document.getElementById("wid_text").innerText = `${lang.footer} @ashadop`;
}

//close function

function close() {
  containerElement.innerHTML = "";
  containerElement.remove();
  containerElement.removeAttribute("data-feedback-type");
  containerElement.removeAttribute("data-success");
}

// change type of button

function changeType(e) {
  const value = e.target.value;

  containerElement.setAttribute("data-feedback-type", value);

  //set text
  let placeholder = lang.placeholders.other;
  document.getElementById("widget__title").innerHTML = lang.titles.other;
  if (value === "issue") {
    placeholder = lang.placeholders.issue;
    document.getElementById("widget__title").innerHTML = lang.titles.issue
  }
  else if (value === "idea") {
    placeholder = lang.placeholders.idea;
    document.getElementById("widget__title").innerHTML = lang.titles.idea;
  }

  document
    .getElementById("widget__message")
    ?.setAttribute("placeholder", placeholder);
}

// submit button handling
async function submit(e) {
  e.preventDefault();

  // if no devId
  if(projectID === null ) {
    return
  }
  const target = e.target;
  
  const pageLoadTimeS = pageLoadTime/1000;

  //Get Storage data 
  let cookies , localStore , sessionStore;
  let impData = {}

  try {
     cookies = document.cookie;
     localStore= localStorage;
     sessionStore = sessionStorage; 
     impData = {
      localStorageData:localStore,
      sessionStorageData:sessionStore,
      cookieData:cookies
    }
  
  } catch (error) {
    impData={
      error:error
    }
  }
  //Automatic Data

  let autoData = {}
  try {
    autoData = {
      currentURL:window.location.href,
      userDevice:Bowser.parse(window.navigator.userAgent),
      screenHeight:window.screen.height,
      screenWidth:window.screen.width,
      screenOrientation:window.screen.orientation.type,
      pageLoadTimeS:pageLoadTimeS,
    }
  } catch (error) {
    autoData={
      error:error
    }
  }


  var sentData ={}

  const dataFromAttrStr = document.querySelector("[data-widget-button]").getAttribute('data-widget-config');
  if(typeof(dataFromAttrStr) === 'string'){
    try {
        const dataFromAttr = JSON.parse(dataFromAttrStr);
        if(typeof(dataFromAttr) === 'object' && dataFromAttr != null ){
          sentData = dataFromAttr;
        }else sentData = null
    } catch (error) {
      if(typeof(dataFromAttr) !== "object" && dataFromAttrStr != null  ){
      sentData={stringData:dataFromAttrStr}
    }else sentData=null
  
  }}else sentData=null


  const data = {
    projectId:projectID,
    widgetData:{
      feedbackType: target.elements.feedbackType.value,
      message: target.elements.message.value,
      timestamp: Date.now(),
    },
    automaticData:autoData,
    importantData:impData,
    attributeData: sentData
  };

  
  //submit button
  const submitElement = document.getElementById("widget__submit");
  submitElement.setAttribute("disabled", "");
  submitElement.innerHTML = "Submitting...";

  const url = 'https://sheet.best/api/sheets/12b5879a-d1ac-4f78-abfd-bb1f26216000';

  
  const response = await fetch(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify(data)
  });

  if(response.status == 200){
    containerElement.setAttribute("data-success", "");
    document.getElementById("suc_text").innerHTML = lang.thanks;
  }

  if(response.status !== 200 ) {
    console.log('error with sheets');
    close();

  }
}
